nav {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  background: white;
  font-weight: 600;
  font-family: 'Space Mono', monospace;
  position: sticky;
  top: 0;
  border-bottom: solid black 2px;

}

.nav__authLinks{
  display: flex;
  align-items: center;
}

.nav__projectName {
  color: #e4f9ff;
  text-decoration: none;
}

.authLink {
  text-decoration: none;
  transition: color 0.2s ease-in-out;
   margin-left: 10px;
}

.authLink {
  padding: 20px;
  margin: 20px;
}

.authLink:hover {
  color: #d8fc94;
}

#fa {
  display: flex;
  align-items: center;
}

#length {
  margin: 0;
  background-color: #ff8166;
  border-radius: 100px;
  padding: 3px 7px;
  color: black;
}

.nav-logoutbtn {
  font-family: inherit;
  font-weight: inherit;
  margin-left: 1em;
  background-color: #5595ff;
  box-shadow: 0 0 5px 0 aliceblue;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  color: #e3f2ff;
  transition: all 0.2s ease-in-out;
  height: 20px;
  width: 70px
}

.nav-logoutbtn:hover {
  transform: scale(1.05);
  background-color: #d13e3e;
}

Link {
  margin-left: 10px;
}

.snitch {
  margin-top: 30px;
  color: black;
  
}

.snitch:hover {
  color: gray;
}


a:visited { text-decoration: none; color:black; }
a:hover { text-decoration: none; color:black; }
a:focus { text-decoration: none; color:black; }
a:hover, a:active { text-decoration: none; color:black }
a {
  text-decoration: none;
}

h1 a {
  text-decoration: none;
}


.wrapper{
  position: relative;
  }
  .wrapper span{
  position: absolute;
  top: -2px;
  right: -2px; 
  }